import React from "react"
import { Section, Layout, Heading, Empty } from "components"
import { useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { fetchEcommerceProducts, fetchShopCategories, fetchEcommerceSites } from "store/actions/contentfulActions"
import { List, Card, Typography, Avatar, Tooltip, Tag, Select, Row, Col } from "antd"
import { countryMap } from "helpers/countryMapping"
import { country } from "App"
import { pricer, mobile } from "helpers"
import styled from "styled-components"
import { Suspense } from "react"
import { useState } from "react"
import { baseStyles } from "styles/base"
import HowtoModal from "./HowtoModal"
import { Trans } from "@lingui/macro"

const ModalDetail = React.lazy(() => import("./Modal"))

const { Paragraph, Text } = Typography

const TheList = styled(List)`
	&& {
		margin-bottom: 3em;
	}
	&.affiliates {
		.ant-row {
			.ant-col {
				margin-bottom: 2.5em;
			}
		}
	}
	.ant-list-header {
		margin-bottom: 2em;
	}
	.ant-row {
		display: flex;
		flex-wrap: wrap;
		> .ant-col {
			margin-bottom: 1em;
		}
	}
`

const ListItem = styled(List.Item)`
	position: relative;
	height: 100%;
	&.list_item--affiliates {
		height: 100px;
		.ant-tag {
			top: -8px;
			right: 0;
			left: unset;
			height: 30px;
			width: 30px;
			border-radius: 100px;
			line-height: 2.4;
			padding-left: 2px;
			font-weight: bold;
		}
		.ant-card {
			border-radius: 10px;
			margin-bottom: 0.5em;
			.ant-card-cover {
				height: 100%;
			}
			img {
				border-radius: 10px;
				height: 100%;
				object-fit: contain;
			}
		}
	}
	.ant-tag {
		position: absolute;
		top: 5px;
		left: 5px;
		z-index: 5;
	}
	.ant-card {
		height: 100%;
		margin-bottom: 1em;
		.ant-card-body {
			padding: 14px;
		}
	}
	article.ant-typography {
		.title {
			line-height: 1.1;
			margin-bottom: 0;
			font-size: 1em;
		}
	}
`

const initialFilteringProps = { category: "" }

export default function StudyShop() {
	const dispatch = useDispatch()
	const products = useSelector(({ contentful }) => contentful.products)
	const loading = useSelector(({ contentful }) => contentful.loading)
	const categoryOptions = useSelector(({ contentful }) => contentful.categoryOptions)
	const ecommerceSites = useSelector(({ contentful }) => contentful.ecommerceSites)
	const [selectedProduct, setSelectedProduct] = useState({})
	const [filteringProps, setFilteringProps] = useState(initialFilteringProps)
	const [howtoModal, setHowtoModal] = useState(false)

	const currentCountry = countryMap[country]

	const handleSelectProduct = (product) => {
		setSelectedProduct({ ...product, symbol: currentCountry.symbol })
	}

	useEffect(() => {
		dispatch(fetchEcommerceProducts(filteringProps))
		dispatch(fetchShopCategories())
		dispatch(fetchEcommerceSites())
		window.scrollTo(0, 0)
	}, [dispatch, filteringProps])

	return (
		<Layout>
			<Suspense>
				<ModalDetail visible={Object.keys(selectedProduct).length} data={selectedProduct} onCancel={() => setSelectedProduct({})} />
				<HowtoModal visible={howtoModal} onCancel={() => setHowtoModal(false)} />
				<Section width={mobile ? "100%" : "85%"} centered>
					<Row type="flex" justify="space-between" className={mobile && "mb2em"}>
						<Col lg={12} xs={24}>
							<Heading
								bold
								content="Study shop"
								subheader={
									<Typography>
										<Trans>
											Where you can shop for study essentials, through our partners, and earn rebates on Cudy.
										</Trans>{" "}
										<Text type="secondary" className="cursor-pointer" underline onClick={() => setHowtoModal(true)}>
											<Trans>Learn more</Trans>
										</Text>
									</Typography>
								}
								marginBottom={mobile ? "1em" : "3em"}
							/>
						</Col>
						<Col lg={12} xs={24} className="ta-right">
							<Select
								style={{ width: mobile ? "100%" : 200 }}
								placeholder="Filter category"
								onChange={(category) => setFilteringProps((prev) => ({ ...prev, category }))}
							>
								{[{ value: "", label: "All" }, ...categoryOptions].map((item) => (
									<Select.Option key={item.value} value={item.value}>
										{item.label}
									</Select.Option>
								))}
							</Select>
						</Col>
					</Row>
					<TheList
						itemLayout="horizontal"
						grid={{ gutter: 16, lg: 6, xs: 2 }}
						dataSource={products}
						loading={loading}
						locale={{ emptyText: <Empty description={<Trans>No products found</Trans>} /> }}
						renderItem={(item) => {
							const listing = item.siteListings || {}
							const image = item.images[0].file || {}
							return (
								<ListItem onClick={() => handleSelectProduct(item)}>
									<Tag color="orange">{item.category}</Tag>
									<Card hoverable cover={<img src={image.url} alt={item.title} />}>
										<Heading
											bold
											level={4}
											marginBottom="0"
											content={<Paragraph ellipsis={{ rows: 2 }}>{item.title}</Paragraph>}
											subheader={
												<Typography>
													<Paragraph>
														{currentCountry.symbol} {pricer(item.prices)}
													</Paragraph>
													<Tooltip title={listing.name}>
														<Avatar
															size={mobile ? "large" : "small"}
															src={listing.logoUrl}
															alt={listing.name}
														/>
													</Tooltip>
												</Typography>
											}
										/>
									</Card>
								</ListItem>
							)
						}}
					/>
					<TheList
						className="affiliates"
						header={<Trans>Our partners</Trans>}
						dataSource={ecommerceSites}
						grid={{ gutter: [16, 16], lg: 6, xs: 4, xxl: 8 }}
						renderItem={(item) => (
							<ListItem className="list_item--affiliates">
								<Tag color={baseStyles.greenColor.primary}>{item.rebate.percent}</Tag>
								<a href={item.siteUrl} target="_blank" rel="noopener noreferrer">
									<Card hoverable cover={<img src={item.logoUrl} alt={item.name} />} />
									<Typography className="ta-center">
										<Typography.Text>{item.name}</Typography.Text>
										<Typography.Paragraph type="secondary">{item.country}</Typography.Paragraph>
									</Typography>
								</a>
							</ListItem>
						)}
					/>
				</Section>
			</Suspense>
		</Layout>
	)
}
