import React, { useState } from "react"
import { Modal } from "components"
import { Steps, Row, Col, Typography } from "antd"
import { Trans } from "@lingui/macro"
import { Section } from "components"
import { ButtonLink } from "components"
import styled from "styled-components"
import browseItems from "assets/images/howto/browse_items.svg"
import dancing from "assets/images/howto/dancing.svg"
import dogJump from "assets/images/howto/dog-jump.svg"
import makePurchase from "assets/images/howto/make_purchase.svg"
import readingSide from "assets/images/howto/reading-side.svg"
import selfie from "assets/images/howto/selfie.svg"
import { Button } from "components"

const stepsContent = [
	{
		title: <Trans>Sign in</Trans>,
		content: <Trans>Sign in to your Cudy account</Trans>,
		image: readingSide,
		bg: "mediumpurple",
		isDark: true,
	},
	{
		title: <Trans>Browse items</Trans>,
		content: <Trans>Browse by featured items or e-commerce sites.</Trans>,
		image: browseItems,
		bg: "aliceblue",
		isDark: false,
	},
	{
		title: "Select an item",
		content: <Trans>Click on the item or e-commerce site you wish to make a purchase</Trans>,
		image: dogJump,
		bg: "floralwhite",
		isDark: false,
	},
	{
		title: <Trans>E-commerce site</Trans>,
		content: <Trans>You will be redirected to the e-commerce page with a tracked URL</Trans>,
		image: readingSide,
		bg: "aliceblue",
		isDark: false,
	},
	{
		title: <Trans>Buy</Trans>,
		content: <Trans>Make your purchase using your e-commerce site personal account</Trans>,
		image: dancing,
		bg: "honeydew",
		isDark: false,
	},
	{
		title: <Trans>Done!</Trans>,
		content: <Trans>Your purchase will be tracked and rebate will be credited to your Cudy account in a few weeks</Trans>,
		image: selfie,
		bg: "lavender",
		isDark: false,
	},
]

const StyledModal = styled(Modal)`
	.ant-modal-body {
		padding: 0;
		> .section {
			padding: 2em 1em;
		}

		.ant-steps-vertical {
			.ant-steps-item {
				min-height: 100px;
			}
			.ant-steps-item-title {
				line-height: 20px;
			}
		}
	}
`

export default function HowtoModal(props) {
	const [current, setCurrent] = useState(0)
	const isLastSlide = current === stepsContent.length - 1

	return (
		<StyledModal {...props} width={760} footer={false}>
			<Row type="flex">
				<Col lg={6}>
					<Section className="section pr1em">
						<Steps current={current} direction="vertical" className="mb4em">
							{stepsContent.map((item) => (
								<Steps.Step key={item.title} title={item.title} />
							))}
						</Steps>
						<Row type="flex">
							<Col>
								<ButtonLink
									icon="left"
									shape="circle"
									onClick={() => setCurrent((prev) => prev - 1)}
									disabled={current === 0}
								/>
							</Col>
							<Col className="ta-right">
								<ButtonLink
									icon="right"
									shape="circle"
									onClick={() => setCurrent((prev) => prev + 1)}
									disabled={isLastSlide}
								/>
							</Col>
						</Row>
					</Section>
				</Col>
				<Col lg={18} className="ta-center">
					<Section bg={stepsContent[current].bg} className="section height-100 ta-center">
						<Section centered className="mb4em" textAlign="center">
							<img src={stepsContent[current].image} width={300} />
						</Section>
						<Typography.Title level={3} className={stepsContent[current].isDark && "white"}>
							{stepsContent[current].title}
						</Typography.Title>
						<Typography.Paragraph className="mb2em">{stepsContent[current].content}</Typography.Paragraph>
						{isLastSlide && (
							<Button type="primary" block onClick={props.onCancel}>
								Okay, got it
							</Button>
						)}
					</Section>
				</Col>
			</Row>
		</StyledModal>
	)
}
